        fw.faces.add(this);
        void pipeline(() => this.txLoop(), tap((pkt) => fw.emit("pkttx", this, pkt)), duplexFromRxTx(rxtx), tap((pkt) => fw.emit("pktrx", this, pkt)), this.rxLoop);
        rxtx.on?.("up", this.handleLowerUp);
        rxtx.on?.("down", this.handleLowerDown);
